/**************************/
/* All synced transitions */
/**************************/
.zoomer { transition: transform 0.5s; transition-timing-function: cubic-bezier(0.7, 0, 0.3, 1); }

.zoomer.zoomer--notrans { transition: none; }

.zoomer__image { transition: opacity 0.3s 0.3s; }

