.content--open, .content__item--current { transition: none; }

.content__item-img { transition: transform 0.4s; transition-timing-function: cubic-bezier(0.7, 1, 0.8, 1); }

.content__item--current .content__item-img { transition-timing-function: cubic-bezier(0.2, 1, 0.3, 1); transition-duration: 1s; }

